import React, { Component } from 'react';

import { InputWrapper, URLInputField, InputField, Button } from './styled';

import { fetchSchema } from '../common';

interface Props {
  parentCallback(value: string): void;
}

interface State {
  link: string;
  username: string;
  password: string;
}

class FetchSchema extends Component<Props, State> {
  state = {
    link: '',
    username: '',
    password: '',
  };

  render() {
    const { link } = this.state;
    const { username } = this.state;
    const { password } = this.state;

    return (
      <InputWrapper>
        <URLInputField
          value={link}
          placeholder="URL of Async API"
          onChange={(e: any) => this.setState({ link: e.target.value })}
        />
        <InputField
          value={username}
          placeholder="Your username"
          onChange={(e: any) => this.setState({ username: e.target.value })}
        />
        <InputField
          value={password}
          placeholder="Your password"
          type="password"
          onChange={(e: any) => this.setState({ password: e.target.value })}
        />
        <Button type="button" onClick={this.fetchSchemaFromExternalResources}>
          Load API Spec
        </Button>
      </InputWrapper>
    );
  }

  private fetchSchemaFromExternalResources = async () => {
    try {
      // tslint:disable-next-line
      new URL(this.state.link);
    } catch (e) {
      return;
    }
    const {
      props: { parentCallback },
      state: { link, username, password },
    } = this;
    parentCallback(await fetchSchema(link, username, password));
  };
}

export default FetchSchema;
